<template>
  <div
    class="popup__wrap on"
    v-if="prescriptionPopFlag === 'Open'"
    ref="medicineAddPopup"
  >
    <div class="popup__bg"></div>
    <div class="common__popup prescirption__template__popup box__shadow__02">
      <div class="popup__top">
        <h4>처방전 템플릿 불러오기</h4>
        <button class="pop__close__btn" @click="closePopup"></button>
      </div>
      <div class="popup__main">
        <div class="grid__wrap">
          <Grid
            class="on"
            ref="gridEl"
            :cols="cols"
            :pagination="true"
            :sort="true"
            :rows="prescriptionTemList"
          ></Grid>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Grid } from "gridjs-vue";
export default {
  props: {
    prescriptionPopFlag: String,
    prescriptionTemList: Array,
  },
  data() {
    return {
      cols: [
        { name: "제목", id: "name" },
        { name: "설명", id: "description" },
        {
          name: "치료제 수",
          id: "prescriptionListByWeek",
          formatter: (cell) => {
            let size = 0;
            for (let i of cell) {
              size = size + i.treatmentList.length;
            }
            return size;
          },
        },
        { name: "등록자명", id: "registerName" },
        { name: "변경일시", id: "updateDate" },
        {
          name: "템플릿 ID",
          id: "pTemplateId",
          hidden: true,
        },
        {
          name: "선택",
          formatter: (cell, row) => {
            return this.$gridjs.h(
              "button",
              {
                onClick: () => {
                  this.setPrescriptionTem(row.cells[2].data);
                  this.closePopup();
                },
              },
              "선택"
            );
          },
          sort: false,
        },
      ],
    };
  },
  components: {
    Grid,
  },
  methods: {
    closePopup() {
      this.$emit("prescriptionTemPopClose", "Close");
    },
    setPrescriptionTem(param) {
      this.$emit("setPrescriptionTem", param);
    },
  },
};
</script>
