<template>
  <main class="prescription">
    <PrescriptionTemplatePopup
      :prescriptionPopFlag="prescriptionPopFlag"
      :prescriptionTemList="prescriptionTemList"
      @prescriptionTemPopClose="prescriptionTemPopClose"
      @setPrescriptionTem="setPrescriptionTem"
    />
    <TemplateSavePopup
      :popupEventFlag="savePopFlag"
      :popupState="savePopupState"
      @templateSavePopClose="templateSavePopClose"
      :prescriptionListByWeek="prescriptionListByWeek"
    />
    <TreatmentPreviewPopup
      :popupEventFlag="previewPopFlag"
      @treatmentPreviewPopClose="treatmentPreviewPopClose"
      :treatmentInfo="treatmentPreview"
    />
    <Prescription
      :dragRemedyItem="dragRemedyItem"
      @treatmentPreview="treatmentPreviewPopOpen"
      @templateSavePopOpen="templateSavePopOpen"
      @prescriptionTemPopOpen="prescriptionTemPopOpen"
      ref="prescription"
    />
    <Remedy @remedyStartDrag="remedyStartDrag" />
  </main>
</template>
<script>
import "@/assets/css/prescription.css";
import "@/assets/css/print.css";
import PrescriptionTemplatePopup from "@/components/PrescriptionCom/PrescriptionTemplatePopup";
import TreatmentPreviewPopup from "@/components/PrescriptionCom/TreatmentPreviewPopup";
import TemplateSavePopup from "@/components/PrescriptionTemplate/TemplateSavePopup";
import Prescription from "@/components/PrescriptionCom/Prescription_Com";
import Remedy from "@/components/PrescriptionCom/Remedy_Com";

export default {
  data() {
    return {
      dragRemedyItem: "",
      previewPopFlag: false,
      treatmentPreview: { name: "" },
      savePopFlag: "Close",
      prescriptionPopFlag: "Close",
      savePopupState: "등록",
      prescriptionListByWeek: [],
      prescriptionTemList: [],
    };
  },
  components: {
    Prescription,
    Remedy,
    TreatmentPreviewPopup,
    TemplateSavePopup,
    PrescriptionTemplatePopup,
  },
  methods: {
    remedyStartDrag(param) {
      this.dragRemedyItem = param;
    },
    treatmentPreviewPopOpen(param) {
      this.previewPopFlag = true;
      Object.assign(this.treatmentPreview, param);
    },
    treatmentPreviewPopClose() {
      this.previewPopFlag = false;
    },
    templateSavePopOpen(param) {
      this.prescriptionListByWeek = param;
      this.savePopFlag = "Open";
    },
    templateSavePopClose() {
      this.savePopFlag = "Close";
    },
    prescriptionTemPopOpen(param) {
      this.prescriptionPopFlag = "Open";
      this.prescriptionTemList = param;
    },
    prescriptionTemPopClose() {
      this.prescriptionPopFlag = "Close";
    },
    setPrescriptionTem(param) {
      this.$refs.prescription.setPrescriptionTem(param);
    },
  },
};
</script>
