<template>
  <section class="prescription__wrap box__shadow__01 vertical__scroll">
    <div class="popup__wrap" :class="{ on: loading }">
      <div class="popup__bg">
        <LoadingBar class="loading" />
      </div>
    </div>
    <PrintBox ref="printBox" />
    <div class="prescription__top">
      <h3 class="f__bold">처방전</h3>
      <ul>
        <li>
          <button class="custom__btn" @click="prescriptionUpdate">
            <p>처방전 저장</p>
          </button>
        </li>
        <li>
          <button
            class="custom__btn"
            @click="getTemplate"
            :class="{ off__btn: status === '발행' }"
          >
            <p>템플릿 불러오기</p>
          </button>
        </li>
        <li>
          <button
            class="custom__btn"
            @click="prescriptionTemSave"
            :class="{ off__btn: status === '발행' }"
          >
            <p>템플릿 저장</p>
          </button>
        </li>
        <li>
          <button
            class="custom__btn"
            @click="prescriptionReset"
            :class="{
              off__btn:
                patientObj.accessCode === '' ||
                patientObj.accessCode === null ||
                patientObj.accessCode === undefined,
            }"
          >
            <p>초기화</p>
          </button>
        </li>
      </ul>
    </div>
    <div class="prescription__middle">
      <p>
        {{ this.$route.params.name }} ({{ this.$route.params.sex }},
        {{ this.$route.params.age }}세)
      </p>
      <div>
        <dl>
          <dt class="f__medium">치료시작일</dt>
          <dd>
            <input
              type="text"
              :value="startdateInput"
              readonly
              class="read__only yellow"
            />
          </dd>
        </dl>
        <dl v-if="status !== '발행'">
          <dt class="f__medium">발행일</dt>
          <dd class="calendar__wrap top">
            <input
              type="text"
              class="date__input"
              :value="prescriptionDateInput"
              readonly
            />
            <span class="calendar__icon" @click="calendarIconClick">
              <img
                src="@/assets/images/icon/ic-calendar.svg"
                alt="달력 아이콘"
              />
            </span>
            <VDatePicker
              @input="clickEndDate($event)"
              locale="ko"
              color="gray"
              :value="prescriptionDateInput"
              :class="{ active: calendarShow }"
              is-required
            />
          </dd>
        </dl>
        <button
          class="custom__btn"
          @click="prescriptionPublish"
          :class="{ off__btn: status === '발행' }"
        >
          <p>처방전 발행</p>
        </button>
        <button
          class="custom__btn"
          @click="runPrint"
          :class="{ off__btn: status !== '발행' }"
        >
          <p>출력</p>
        </button>
      </div>
    </div>
    <div class="prescription__bottom">
      <div
        class="week__list__item"
        v-for="(week, weekIdx) in patientObj.prescriptionListByWeek"
        :data-index="weekIdx"
        :key="weekIdx"
      >
        <div class="title__wrap">
          <h4 :data-week="weekIdx">{{ weekIdx + 1 }}주차</h4>
          <button
            class="custom__btn delete small"
            @click="cleanWeekData(weekIdx)"
          >
            <span>
              <img src="@/assets/images/icon/ic-remove.svg" alt="삭제 아이콘" />
            </span>
            <p>비우기</p>
          </button>
        </div>
        <ul>
          <li
            class="box__shadow__03"
            v-for="(date, dateIdx) in week.treatmentList"
            :data-index="dateIdx"
            :key="dateIdx"
            @dragover="dragOver($event)"
            @drop="drop($event, 'add')"
            @dblclick="treatmentPreview(date.id, date.dayNum, weekIdx + 1)"
          >
            <p class="day__num" v-if="date.id === ''">
              {{ dateIdx + 1 }}
            </p>
            <dl
              v-if="typeof date.id !== ''"
              draggable="true"
              @dragend="removeDragEnd($event)"
              @dragover="removeDragOver"
              @dragleave="removeDragStart($event)"
            >
              <dt>{{ date.categoryName }}</dt>
              <dd>{{ date.name }}</dd>
            </dl>
          </li>
        </ul>
      </div>
    </div>
  </section>
</template>
<script>
import "@/assets/css/calendar.css";
import dateParse from "@/utill/dateParse.js";
import PrintBox from "@/components/PrescriptionCom/PrintBox";
import VDatePicker from "v-calendar/lib/components/date-picker.umd";
import {
  addPrescription,
  updatePrescription,
  // resetPrescription,
  prescriptionPublish,
  getTreatmentPreview,
  getPrescriptionTemplate,
} from "@/api/patient";
import LoadingBar from "@/components/common/LoadingBar";

export default {
  props: {
    dragRemedyItem: [Object, String],
  },
  data() {
    return {
      patientObj: {
        patientRegNumber: "",
        subjectId: "",
        prescriptionDate: "", // 처방전 발행일자
        startDate: "", // 교육 시작일
        startLevel: "1",
        prescriptionListByWeek: "",
      },
      dargItemAddFlag: false,
      dargItemRemoveFlag: true,
      startdateInput: "",
      prescriptionDateInput: "",
      status: "",
      calendarShow: false,
      treatmentPreviewFlag: false,
      loading: false,
    };
  },
  watch: {
    dragRemedyItem() {
      this.dargItemAddFlag = true;
    },
  },
  components: {
    PrintBox,
    VDatePicker,
    LoadingBar,
  },
  methods: {
    setPrescriptionTem(param) {
      this.patientObj.prescriptionListByWeek =
        this.$store.state.prescription(0);
      this.setPrescription(param);
    },
    getTemplate() {
      this.loading = true;
      getPrescriptionTemplate("", "Y", "")
        .then((response) => {
          this.loading = false;
          this.$emit("prescriptionTemPopOpen", response.data.response);
        })
        .catch((error) => this.$toast.error(error.message));
    },
    cleanWeekData(param) {
      if (this.status === "발행") {
        this.prescriptionNotice();
      } else {
        this.patientObj.prescriptionListByWeek[param].treatmentList.map(
          (obj, idx) => {
            this.patientObj.prescriptionListByWeek[param].treatmentList.splice(
              idx,
              1,
              {
                dayNum: 1,
                id: "",
                preLearningWeekNum: null,
                preLearningDayNum: null,
                categoryName: "",
                name: "",
              }
            );
          }
        );
        this.treatmentPreviewFlag = false;
      }
    },
    calendarIconClick() {
      if (this.status !== "발행") {
        this.calendarShow === false
          ? (this.calendarShow = true)
          : (this.calendarShow = false);
      } else {
        this.prescriptionNotice();
      }
    },
    prescriptionNotice() {
      this.$confirm({
        // title: "",
        message: "이미 발행된 처방전은 편집이 불가능합니다.",
        button: {
          yes: "확인",
        },
      });
    },
    clickEndDate(param) {
      this.patientObj.prescriptionDate = dateParse.dateToString2(param);
      this.setTimeInput();
      this.calendarShow = false;
      if (
        Number(this.patientObj.prescriptionDate) <
        Number(dateParse.dateToString2(new Date()))
      ) {
        this.$confirm({
          // title: "",
          message: "발행일을 금일 또는 이후날짜로 지정해주세요.",
          button: {
            yes: "확인",
          },
        });
        this.patientObj.prescriptionDate = "";
        this.prescriptionDateInput = "";
      }
      // this.patientObj.prescriptionDate
    },
    dragOver(evt) {
      evt.preventDefault();
    },
    drop(evt) {
      if (this.status === "발행") {
        this.prescriptionNotice();
      } else {
        if (this.dargItemAddFlag) {
          evt.preventDefault();
          if (evt.path[1].childNodes[0].tagName === "P") {
            const weekIndex = Number(
              evt.path[3].childNodes[0].childNodes[0].dataset.week
            );
            const dateIndex = Number(evt.path[1].childNodes[0].textContent) - 1;
            const treatmentObj =
              this.patientObj.prescriptionListByWeek[weekIndex].treatmentList[
                dateIndex
              ];
            if (this.$env == "development") console.log(treatmentObj);
            treatmentObj.id = this.dragRemedyItem.id;
            treatmentObj.categoryName = this.dragRemedyItem.categoryName;
            treatmentObj.name = this.dragRemedyItem.name;
            treatmentObj.dayNum = dateIndex;
          }
          this.dargItemAddFlag = false;
          this.treatmentPreviewFlag = false;
        }
      }
    },
    removeDragOver() {
      // if (this.$env == "development") console.log("드래그 들어옴");
      this.dargItemRemoveFlag = true;
    },
    removeDragEnd(evt) {
      if (this.status === "발행") {
        this.prescriptionNotice();
      } else {
        if (!this.dargItemRemoveFlag) {
          const weekIndex = Number(evt.path[3].dataset.index);
          const dateIndex = Number(evt.path[1].dataset.index);
          this.patientObj.prescriptionListByWeek[
            weekIndex
          ].treatmentList.splice(dateIndex, 1, { id: "" });
        }
        this.treatmentPreviewFlag = false;
      }
    },
    removeDragStart() {
      // if (this.$env == "development") console.log("드래그 벗어남");
      this.dargItemRemoveFlag = false;
    },
    runPrint() {
      window.print();
    },
    setTimeInput() {
      if (this.patientObj.startDate !== null) {
        // this.patientObj.startDate = this.getTodayStr();
        this.startdateInput = this.patientObj.startDate.replace(
          /(\d{4})(\d{2})(\d{2})/,
          "$1-$2-$3"
        );
      }
      if (this.patientObj.prescriptionDate !== null) {
        this.prescriptionDateInput = this.patientObj.prescriptionDate.replace(
          /(\d{4})(\d{2})(\d{2})/,
          "$1-$2-$3"
        );
      }
    },
    getTodayStr() {
      let today = "";
      today = new Date();
      const startDate = new Date(today.setDate(today.getDate() + 1));
      return dateParse.dateToString2(startDate);
    },
    prescriptionUpdate() {
      const obj = {};
      obj.subjectId = this.patientObj.subjectId;
      obj.prescriptionDate = null;
      obj.startDate = null;
      obj.startLevel = this.patientObj.startLevel;
      let prescriptionListByWeek = [];
      for (let i = 0; i < 12; i++) {
        let treatmentCheck = 0;
        let treatmentList = [];
        for (let j = 0; j < 7; j++) {
          if (
            this.patientObj.prescriptionListByWeek[i].treatmentList[j].id !== ""
          ) {
            treatmentCheck++;
            treatmentList.push(
              this.patientObj.prescriptionListByWeek[i].treatmentList[j]
            );
          }
        }
        if (treatmentCheck >= 1) {
          prescriptionListByWeek.push({
            treatmentList: treatmentList,
            weekNum: i + 1,
          });
        }
      }
      obj.prescriptionListByWeek = prescriptionListByWeek;
      if (prescriptionListByWeek.length > 0) {
        this.loading = true;
        switch (this.status) {
          case "등록":
            addPrescription(obj, this.patientObj.patientRegNumber)
              .then((response) => {
                this.$toast.success("처방전이 등록되었습니다");
                this.patientObj.accessCode = response.data.response.accessCode;
                this.$route.params.accessCode =
                  response.data.response.accessCode;
                this.status = "수정";
                this.loading = false;
              })
              .catch((error) => this.$toast.error(error.message));
            break;
          case "수정":
            obj.accessCode = this.patientObj.accessCode;
            updatePrescription(obj, this.patientObj.patientRegNumber)
              .then((response) => {
                this.$toast.success("처방전이 수정되었습니다.");
                this.loading = false;
                if (this.$env == "development") console.log(response);
              })
              .catch((error) => {
                this.$toast.error(error.message);
                if (this.$env == "development") console.log(error);
              });
            break;
          case "발행":
            this.prescriptionNotice();
            this.loading = false;
            break;
        }
      } else {
        this.$confirm({
          // title: "",
          message: "치료제를 하나이상 등록해주세요.",
          button: {
            yes: "확인",
          },
        });
      }
    },
    prescriptionPublish() {
      const param = {
        accessCode: this.patientObj.accessCode,
        prescriptionDate: this.patientObj.prescriptionDate,
      };

      if (param.prescriptionDate === "") {
        this.$toast.error("발행일을 지정해주세요");
      } else {
        if (
          this.patientObj.accessCode !== null &&
          this.patientObj.accessCode !== "" &&
          this.patientObj.accessCode !== undefined
        ) {
          this.loading = true;
          prescriptionPublish(param, this.patientObj.patientRegNumber)
            .then((res) => {
              if (this.$env == "development") console.log(res);
              this.loading = false;
              this.$toast.success("처방전이 발행되었습니다");
              this.$route.params.issuedYn = "Y";
              this.$router.push({
                name: "PatientDashboard",
                params: this.$route.params,
              });
            })
            .catch((error) => {
              if (this.$env == "development") console.log(error.message);
            });
        } else {
          this.$confirm({
            // title: "",
            message: "발행 전에 편집중인 처방전을 저장해주세요.",
            button: {
              yes: "확인",
            },
          });
        }
      }
    },
    prescriptionReset() {
      // const param = {
      //   accessCode: this.patientObj.accessCode,
      //   patientRegNumber: this.patientObj.patientRegNumber,
      // };
      this.$confirm({
        // showCancelButton: true,
        title: "초방전을 초기화 하시겠습니까?",
        message: "초기화 안내문구....",
        button: {
          no: "아니오",
          yes: "네",
        },
        callback: (confirm) => {
          if (confirm) {
            // resetPrescription(param)
            //   .then((response) => {
            //     this.$toast.success("처방전이 초기화 되었습니다.");
            //     this.$router.push({
            //       name: "PatientInquiry",
            //     });
            //     if (this.$env == "development") console.log(response);
            //   })
            //   .catch((error) => {
            //     this.$toast.error(error.message);
            //     if (this.$env == "development") console.log(error);
            //   });
          }
        },
      });
    },
    treatmentPreview(id, day, week) {
      if (this.treatmentPreviewFlag === true) {
        if (id !== "") {
          // if (this.status === "발행") {
          this.loading = true;
          getTreatmentPreview(this.patientObj.patientRegNumber, week, day)
            .then((response) => {
              this.loading = false;
              this.$emit("treatmentPreview", response.data.response);
              if (this.$env == "development") console.log(response);
            })
            .catch((error) => {
              this.$toast.error(error.message);
              if (this.$env == "development") console.log(error);
              this.loading = false;
            });
          // }
        }
      } else {
        this.$confirm({
          message: "치료제 미리보기는 처방전을 발행후 가능합니다.",
          button: {
            yes: "확인",
          },
        });
        this.loading = false;
      }
    },
    prescriptionTemSave() {
      this.$emit(
        "templateSavePopOpen",
        this.patientObj.prescriptionListByWeek.slice()
      );
    },
    setPrescription(prescriptionListByWeek) {
      for (let i = 0; i < prescriptionListByWeek.length; i++) {
        let weekNum =
          prescriptionListByWeek.length === 1
            ? 0
            : prescriptionListByWeek[i].weekNum - 1;
        for (
          let j = 0;
          j < prescriptionListByWeek[i].treatmentList.length;
          j++
        ) {
          if (prescriptionListByWeek[i].treatmentList[j].id !== null) {
            this.patientObj.prescriptionListByWeek[weekNum].treatmentList.map(
              (obj, idx) => {
                if (
                  obj.dayNum ===
                  prescriptionListByWeek[weekNum].treatmentList[j].dayNum
                ) {
                  Object.assign(
                    this.patientObj.prescriptionListByWeek[weekNum]
                      .treatmentList[idx],
                    prescriptionListByWeek[weekNum].treatmentList[j]
                  );
                }
              }
            );
          }
        }
      }
    },
  },
  mounted() {
    if (Object.keys(this.$route.params).length === 0) {
      this.$router.push({
        name: "PatientInquiry",
      });
    } else {
      this.patientObj = Object.assign(this.patientObj, this.$route.params);
      if (this.patientObj.accessCode === null) {
        this.status = "등록";
        this.patientObj.prescriptionListByWeek =
          this.$store.state.prescription(0);
      } else {
        if (this.$route.params.startDate) {
          const startDate = this.$route.params.startDate;
          let year = startDate.slice(0, 4);
          let month = startDate.slice(4, 6);
          month = Number(month) - 1;
          let date = startDate[6] + startDate[7];
          this.patientObj.prescriptionListByWeek =
            this.$store.state.prescription(
              new Date(year, month, date).getDay()
            );
        } else {
          this.patientObj.prescriptionListByWeek =
            this.$store.state.prescription(0);
        }
        let prescriptionListByWeek;

        prescriptionListByWeek = this.$route.params.prescriptionListByWeek.sort(
          function (a, b) {
            return a.weekNum - b.weekNum;
          }
        );
        if (this.$env == "development") console.log(this.$route.params);

        this.setPrescription(prescriptionListByWeek);

        if (this.patientObj.issuedYn === "Y") {
          this.status = "발행";
          this.$refs.printBox.setData(this.$route.params, "알콜중독");
          this.treatmentPreviewFlag = true;
        } else {
          this.status = "수정";
        }
      }
      this.setTimeInput();
    }
  },
};
</script>
