<template>
  <section class="remedy__wrap box__shadow__01 vertical__scroll">
    <div class="remedy__top">
      <h3>치료제 조회</h3>
    </div>
    <!-- <div class="remedy__middle">
      <dl>
        <dt>과목선택</dt>
        <dd>
          <select
            name=""
            id=""
            @change="filterRemedy('selectChange', $event)"
            ref="subjectSelector"
          >
            <option value="">과목 전체</option>
            <option
              v-for="subject in treatmentManagement.subject"
              :key="subject.key"
              :value="subject"
            >
              {{ subject }}
            </option>
          </select>
        </dd>
      </dl>
    </div> -->
    <div class="remedy__bottom">
      <div class="title__wrap">
        <h4>카테고리</h4>
        <dl>
          <dt>
            <label for="checkbox01">전체선택</label>
          </dt>
          <dd>
            <input
              type="checkbox"
              @change="categoryAllcheck($event)"
              class="custom__checkbox"
              id="checkbox01"
              v-model="allChecked"
            />
          </dd>
        </dl>
      </div>
      <ul class="category__list" ref="categoryList">
        <li
          v-for="category in treatmentCategoryList"
          :key="category.index"
          :data-code="category.codeId"
          class="f__medium"
          :class="{ on: selectedCategory.includes(category.codeName) }"
          @click="categoryClick(category.codeName)"
        >
          {{ category.codeName }}
        </li>
      </ul>
    </div>
    <div class="remedy__list__wrap">
      <div class="title__wrap">
        <h4>치료제</h4>
        <dl v-if="treatmentList.length > 0">
          <dt>검색결과 &nbsp;&nbsp;:&nbsp;&nbsp;</dt>
          <dd>{{ treatmentList.length }}</dd>
        </dl>
      </div>
      <ul class="remedy__item__wrap" v-if="treatmentList.length > 0">
        <li
          class="box__shadow__03"
          v-for="treatment in treatmentList"
          :key="treatment.updatetime"
        >
          <dl draggable="true" @dragstart="startDrag(treatment)">
            <dt>{{ treatment.categoryName }}</dt>
            <dd class="f__medium">
              {{ treatment.name }}
            </dd>
          </dl>
        </li>
      </ul>
    </div>
  </section>
</template>
<script>
import data3 from "@/data/common/data3.json";
import data11 from "@/data/patientManage/data11.json";
export default {
  data() {
    return {
      itemObj: {},
      treatmentCategoryList: [],
      treatmentList: [],
      treatmentListBack: [],
      selectedCategory: [],
      allChecked: false,
    };
  },
  methods: {
    categoryAllcheck(evt) {
      this.selectedCategory = [];
      if (evt.target.checked) {
        this.treatmentCategoryList.map((obj) => {
          this.selectedCategory.push(obj.codeName);
        });
      }
      this.treatmentFiltering();
    },
    categoryClick(categoryName) {
      if (!this.selectedCategory.includes(categoryName)) {
        this.selectedCategory.push(categoryName);
      } else {
        for (let i = 0; i < this.selectedCategory.length; i++) {
          if (this.selectedCategory[i] === categoryName) {
            this.selectedCategory.splice(i, 1);
            i--;
          }
        }
      }
      if (this.selectedCategory.length === this.treatmentCategoryList.length) {
        this.allChecked = true;
      } else {
        this.allChecked = false;
      }
      this.treatmentFiltering();
    },
    treatmentFiltering() {
      this.treatmentList = this.treatmentListBack;

      this.treatmentList = this.treatmentList.filter((obj) =>
        this.selectedCategory.includes(obj.categoryName)
      );
    },
    startDrag(treatmentInfo) {
      const cloneObj = {};
      for (let key in treatmentInfo) {
        cloneObj[key] = treatmentInfo[key];
      }
      if (this.$env == "development") console.log(cloneObj);
      this.$emit("remedyStartDrag", cloneObj);
    },
    getTreatmentCategoryList() {
      this.treatmentCategoryList = data11;
      console.log(data11);
    },
    getTreatments() {
      this.treatmentListBack = data3;
      this.treatmentList.forEach((treatment, index) => {
        treatment.index = index + 1;
      });
    },
  },
  mounted() {
    this.getTreatmentCategoryList();
    this.getTreatments();
  },
};
</script>
